import IntegrationIcons from '@/assets/integration_icons';
import { Component, Host, h, Event, EventEmitter } from '@stencil/core';
import { TAuthNavigation } from '../ir-auth/auth.types';

@Component({
  tag: 'ir-signup',
  styleUrl: 'ir-signup.css',
  shadow: true,
})
export class IrSignup {
  @Event() navigate: EventEmitter<TAuthNavigation>;
  renderFBIcon() {
    return <span slot="left-icon">{IntegrationIcons['001']}</span>;
  }
  render() {
    return (
      <Host>
        <h1 class="title mb-6">Create an account</h1>
        <form class="mb-6 space-y-3">
          <fieldset>
            {/* <label htmlFor="first_name">First name</label> */}
            <ir-input inputId="first_name" placeholder="Enter your first name"></ir-input>
          </fieldset>
          <fieldset>
            {/* <label htmlFor="last_name">Last name</label> */}
            <ir-input inputId="last_name" placeholder="Enter your last name"></ir-input>
          </fieldset>
          <fieldset>
            {/* <label htmlFor="email">Email</label> */}
            <ir-input inputId="email" placeholder="Enter your email"></ir-input>
          </fieldset>
          <fieldset>
            {/* <label htmlFor="password" class="mb-2">
              Password
            </label> */}
            <ir-input inputId="password" type="password" placeholder="Enter your password"></ir-input>
          </fieldset>
          <ir-button class="w-full mt-4" label="Sign up" size="md"></ir-button>
        </form>
        <div class="flex items-center justify-center gap-4 mb-4">
          <div class="h-[1px] w-[45%] bg-[var(--gray-200)]"></div>
          <span class="text-[var(--gray-500)]">OR</span>
          <div class="h-[1px] w-[45%] bg-[var(--gray-200)]"></div>
        </div>
        {/* <ir-button class="w-full" variants="outline" label="Sign up with Google" haveLeftIcon>
            <span slot="left-icon">{IntegrationIcons.google}</span>
          </ir-button> */}
        <ir-button class="w-full" variants="outline" label="Sign up with Facebook" haveLeftIcon>
          {this.renderFBIcon()}
        </ir-button>
        <div class="flex items-center justify-center mt-4">
          <p class="dont-have-an-account">Already have an account?</p>
          <ir-button label="Log in" variants="ghost-primary" onButtonClick={() => this.navigate.emit('login')}></ir-button>
        </div>
      </Host>
    );
  }
}
